@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);

* {
  box-sizing: border-box;
}

body {
  background-color: #1233ff;
}


ul { 
  padding-left: 10px;
}

main {
  background-color: white;
  color: #1233ff;
  font-family: 'Poppins', sans-serif;
}

.nav-main {
  display: grid;
  grid-template-columns: 200px 1fr;
  grid-column-gap: 2px;
  width: 90%;
  margin: 0 auto;
}

aside h3, aside ul{
  font-family: 'Poppins', sans-serif;
  color: white;
  padding: 0;
}

.nav h3 {
  font-weight: bold;
  font-size: 26px;
}

.add-state label {
  font-size: 20px;
}

.add-state ul {
  margin: 0;
}

.gray-bgc {
  background-color: lightgray;
  padding: 0 5px;
}

.field-title {
  border: #1233ff 1px solid;
  border-width: 2px 0;
  font-size: 20px;
  padding: 3px 7px;
}

.state-title {
  font-size: 22px;
  border-radius: 15px 15px 0 0;
  padding: 3px 7px;
  /* text-align: center; */
}

.field-card {
  margin: 0;
}

.state-title {
  margin: 0;
  padding-bottom: 0;
}

.content {
  margin: 5px;
}

.arrows {
  display: float;
  float: right;
  width: 20px;
  height: 20px;
  margin: 3px 3px;
}

.arrows:hover, .label-arrows label:hover {
  cursor: pointer;
}

.big-title {
  display: inline-block;
  margin: 10px 15px;
}

.state-info {
  padding: 0 5px;
}

.label-arrows {
  display: inline-block;
  text-align: right;
}

.title-float-right {
  display: grid;
  /* float: right; */
  margin: 4px;
}


/* body {
  width: 85%;
  margin: 0 auto;
} */

select {
  margin: 5px;
}

.logo {
  width: 250px;
}

